import { Container } from 'react-bootstrap';
import { Row, Col, Button, Nav } from 'react-bootstrap';
import { useDispatch, useSelector } from 'react-redux';
import { useParams } from "react-router-dom";
import styled from 'styled-components';
import { useContext, useEffect, useState } from 'react';
import "../App.css";
import { plusProduct } from '../store';

import { Context1 } from '../App.js';

let YellowBtn = styled.button`
  background: ${props => props.bg};
  color: ${props => props.bg == 'blue' ? 'white' : 'black'};
  padding: 10px;
  border: none;
`

let Br = styled.div`
  margin-bottom: 20px;
`

let Box = styled.div`
  background: grey;
  padding: 20px;
`

function Detail(props) {

  let list = useSelector((state)=>{ return state });
  let dispatch = useDispatch();

  let { inven, shoes } = useContext(Context1);
  let [sec, setSec] = useState(2);
  let [count, setCount] = useState(0);
  let {id} = useParams();
  let findProduct = props.shoes.find((a) => a.id == id);
  let [alert, setAlert] = useState(true);
  let [num, setNum] = useState('');
  let [tab, setTab] = useState(0);
  let [fade, setFade] = useState('');

  useEffect(()=>{
    let copy = localStorage.getItem('watched');
    copy = JSON.parse(copy);
    copy.push(findProduct.id);
    copy = new Set(copy);
    copy = Array.from(copy);
    localStorage.setItem('watched', JSON.stringify(copy));
    // if(copy.includes(findProduct.id) == false){
    //   copy.push(findProduct.id);
    //   localStorage.setItem('watched', JSON.stringify(copy));
    // }
  }, [])

  useEffect(()=>{
    if (isNaN(num) == true){
      window.alert('수량은 숫자만 입력 가능합니다.');
    }
  }, [num])

  useEffect(()=>{
    setFade('end');
    return ()=>{
      setFade('');
    }
  }, [])

  setTimeout(()=>{
    sec -= 1;
    if(sec >= 0){ setSec(sec) }
    if(sec == 0){ setAlert(false) }
  }, 1000)

  /*
    useEffect(()=>{  })         재렌더링 될때마다 실행
    useEffect(()=>{  }, [])     mount시 1회 코드실행
    useEffect(()=>{  }, [sec])  sec가 변경될때 실행
    useEffect(()=>{
      return ()=>{
                                unmount시 1회 코드실행
      }
    }, [])
  */

  return(
    <div className={'start ' + fade}>
      {inven}
      <YellowBtn bg="skyblue">버튼</YellowBtn>
      {count}<button onClick={()=>{ setCount(count+1) }}>버튼</button>
      {alert ? <div className="alert alert-warning">
        {sec}초 이내 구매시 할인
      </div> : null}
      <Container>
        <Row>
          <Col>
            <img src="https://codingapple1.github.io/shop/shoes1.jpg"
            width="80%"/>
            <h4>{findProduct.title}</h4>
            <p>{findProduct.content}</p>
            <p>{findProduct.price}원</p>
            <p>수량: <input onChange={(e)=>setNum(e.target.value)}/></p><Br></Br>
            <Button variant="danger" onClick={()=>{
              dispatch(plusProduct({id: 2, name: 'Red Knit', count: 1}))
            }}>주문하기</Button>
          </Col>
        </Row>
      </Container>
      <Nav fill variant="tabs" defaultActiveKey="link-0">
        <Nav.Item>
          <Nav.Link eventKey="link-0" onClick={()=>{setTab(0)}}>버튼0</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>{setTab(1)}}>버튼1</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={()=>{setTab(2)}}>버튼2</Nav.Link>
        </Nav.Item>
      </Nav>
      <TabContent tab={tab} shoes={props.shoes}/>
    </div>
  )
}

function TabContent({tab}){

  let { inven, shoes } = useContext(Context1);
  let [fade, setFade] = useState('');

  useEffect(()=>{
    setTimeout(()=>{setFade('end');},10);
    return ()=>{
      setFade('');
    }
  }, [tab])

  return(
    <div className={'start ' + fade}>
      {[<div>{shoes[0].title}{inven}</div>, <div>내용1</div>, <div>내용2</div>][tab]}
    </div>
  )
}

export default Detail;